@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Poppins:ital,wght@0,400;0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
:root {
  --bold--: "Montserrat", sans-serif;
  --medium: "Roboto", sans-serif;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body,
html {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

h1,
h2,
h3,
h4 {
  font-family: var(--medium--);
}

p,
a {
  font-family: "Roboto", sans-serif;
  font-family: var(--medium);
}
.parent {
  display: flex;
  flex-direction: column;
  padding: 0 5%;
}

.spinner {
  -webkit-animation: rotate 2s linear infinite;
          animation: rotate 2s linear infinite;
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -25px 0 0 -25px;
  width: 50px;
  height: 50px;
}

.spinner .path {
  stroke: hsl(210, 70, 75);
  stroke-linecap: round;
  -webkit-animation: dash 1.5s ease-in-out infinite;
          animation: dash 1.5s ease-in-out infinite;
}
@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}

.nav-container {
  display: flex;
  flex-direction: row;
}
.nav-header {
  margin-right: auto;
}
.nav-header .logo {
  padding: 15px;
  margin-top: 40px;
  margin-left: 25px;
}

.bars {
  display: none;
  align-items: center;
}
.nav-desk {
  align-self: center;
}

.nav-desk nav ul {
  list-style: none;
  display: flex;
}
.nav-desk nav ul li {
  display: inline;
  display: flex;
  align-items: center;
}
.nav-desk nav ul li a {
  color: #000;
  font-size: 1.5rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  text-decoration: none;
  padding: 10px 10px;
  border: 1px solid transparent;
  margin: 4px 10px;
  transition: 0.3s;
}
.nav-desk nav ul li a:hover {
  color: rgb(39, 39, 53);
}
.cart-icon {
  font-size: 2.8rem;
  padding: 2px;
  cursor: pointer;
}
.cart-icon span {
  font-size: 0.8rem;
  position: absolute;
  -webkit-transform: translate(-20px, 8px);
          transform: translate(-20px, 8px);
}

.nav-mobile {
  display: none;
}

/* for mobile devices */

@media screen and (max-width: 768px) {
  body,
  html {
    overflow-x: hidden;
  }
  .nav-container {
    overflow-x: hidden;
  }
  .bars {
    display: block;
    overflow-x: hidden;
    align-self: center;
    justify-self: baseline;

    display: flex;
    padding: 5px 10px;
  }
  .top {
    align-self: start;

    position: absolute;
    overflow-x: hidden;
    right: 0;
    z-index: 4;
  }
  .top .cart-icon {
    display: none;
  }
  .nav-header .logo {
    margin-right: auto;
    margin-top: 1.3rem;
  }
  .nav-header {
    display: flex;
    padding: 10px;

    overflow-x: hidden;

    width: 100%;
    justify-self: center;
    align-self: center;
  }
  .nav-desk nav ul {
    display: none;
  }

  .nav-mobile {
    position: fixed;
    display: flex;
    height: 100vh;
    background-color: rgb(0, 0, 0);
    color: #ffffff;
    visibility: hidden;
    right: 0;
    width: 0px;
    top: 0;
    z-index: 4;
    overflow-x: hidden;
    transition: 0.3s ease;
  }
  .show {
    visibility: visible;
    width: 33%;
  }
  .nav-mobile ul {
    padding: 2rem;

    list-style: none;
  }
  .nav-mobile ul li {
    padding: 10px 15px;
  }
  .nav-mobile ul li a {
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-weight: 800;
    font-size: 1.8rem;
    text-decoration: none;
    transition: 0.3s;
  }
  .nav-mobile ul li a:hover {
    cursor: pointer;
    color: rgb(51, 48, 48);
  }
}

@media screen and (max-width: 480px) {
  .show {
    visibility: visible;
    width: 100%;
  }
}

