#menuToggle {
  display: block;
  cursor: pointer;
  top: 50px;
  left: 50px;
  width: 50px;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

/*
  * Just a quick hamburger
  */
#menuToggle span {
  display: block;
  width: 23px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;

  background: #cdcdcd;
  border-radius: 3px;

  z-index: -1;

  -webkit-transform-origin: 4px 0px;

          transform-origin: 4px 0px;

  transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease,
    -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease,
    -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

/* 
  * Transform all the slices of hamburger
  * into a crossmark.
  */
.active span {
  opacity: 1;
  -webkit-transform: rotate(-45deg) translate(-2px, -1px);
          transform: rotate(-45deg) translate(-2px, -1px);
  background: #232323;
}

/*
  * .
  */
.active span:nth-last-child(3) {
  opacity: 0;
  -webkit-transform: rotate(0deg) scale(0.2, 0.2);
          transform: rotate(0deg) scale(0.2, 0.2);
}

/*

  */
.active span:nth-last-child(2) {
  -webkit-transform: rotate(45deg) translate(0, -1px);
          transform: rotate(45deg) translate(0, -1px);
}

/*
  *  absolute positioned
  * at the top left of the screen
  */
#menu {
  position: absolute;
  width: 300px;
  margin: -100px 0 0 -50px;
  padding: 50px;
  padding-top: 125px;

  background: #ededed;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  /* to stop flickering of text in safari */

  -webkit-transform-origin: 0% 0%;

          transform-origin: 0% 0%;
  -webkit-transform: translate(-100%, 0);
          transform: translate(-100%, 0);

  transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

#menu li {
  padding: 10px 0;
  font-size: 22px;
}

/*
  * And let's slide it in from the left
  */

.cart-icon {
  width: 45px;
  height: 45px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 4px;
  flex-direction: column;
}
.shopping-icon {
  width: 24px;
  height: 24px;
}

.item-count {
  position: absolute;
  font-size: 10px;
  font-weight: bold;
  bottom: 16px;
  right: -2px;
}

.home-section {
  height: 74vh;
}

.hero {
  display: grid;
  height: 74vh;
  grid-template-columns: repeat(12, 1fr);
}
.content {
  display: flex;
  justify-content: center;
  align-items: center;

  flex-direction: column;
  grid-column: 1 / 7;
}
.content h1 {
  font-size: 2rem;
  padding: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 800;
}
.content h4 {
  color: rgb(111, 111, 134);
  font-style: normal;
  padding: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.image {
  height: 60vh;
  width: 30vw;

  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.7);
}
.circle {
  position: absolute;
  width: 200px;
  background-color: #000000;
  height: 200px;
  top: 27%;
  right: 30%;
  z-index: -1;
  border-radius: 100%;
}
.image-slide {
  grid-column: 7/-1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shop-now-btn {
  color: rgb(0, 0, 0);
  padding: 10px 25px;
  text-decoration: none;
  font-size: larger;
  transition: 0.5s;
  display: block;
  margin-left: 50px;
  padding-left: 15px;
}
.shop-now-btn:hover {
  color: rgb(39, 39, 53);
}

@media screen and (max-width: 680px) {
  .hero {
    display: block;
  }
  .image {
    height: 50vh;
    width: 80vw;
  }
  .circle {
    top: 34%;
    opacity: 0.2;
  }
}

.template {
  display: grid;
  grid-template-columns: repeat(2, 50%);
  grid-gap: 10px;
}

.main-image {
  grid-column: 1;
  overflow: hidden;
}
.btn-grp button {
  color: #fff;
}

.products-section {
  grid-column: 2;
  display: grid;
  align-items: center;
  justify-items: center;
  padding: 0px 10px;
  grid-gap: 10px;
  grid-template-columns: repeat(2, 6fr);
}
.show-added {
  color: rgb(255, 255, 255);
  position: fixed;
  bottom: 0;
  left: 0;
  height: 70px;
  width: 50%;
  display: flex;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  padding: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 0, 0);
}
.product-card {
  height: 400px;
  width: 80%;
  border: 1px solid #eef;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  margin-bottom: 40px;
  margin-top: 10px;
  -webkit-transform: border 0.2s;
          transform: border 0.2s;
  overflow: hidden;
}

.product-img {
  height: 60%;
  border: 1px solid #eef;
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  margin-bottom: auto;
  z-index: -4;
}
.btn-grp {
  display: flex;
  padding-left: 0.3rem;
}
.btn-grp p {
  color: rgb(116, 116, 116);
  padding: 1.1rem;
  align-self: baseline;
  margin-right: auto;
}
.button {
  display: block;
  padding: 10px;
  align-self: baseline;
  border: none;
  color: #fff;
  cursor: pointer;
  background-color: #000;
  color: rgb(0, 0, 0);
  border-radius: 3px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}

/* cart css */
.cart-main {
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 10;
  height: 100vh;
  background-color: rgb(255, 255, 255);
  color: #ffffff;
  box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.2);
  right: 0;
  width: 0px;
  top: 0;

  color: #000;
  overflow-x: hidden;
  transition: width 0.3s ease-in-out;
  overflow-y: auto;
}
.content h3 {
  cursor: pointer;
}

.open-cart {
  width: 30%;
}

.cart-content {
  display: flex;
  padding: 2rem;
  flex-direction: column;
}
.cart-content .emptycart {
  align-self: center;
  padding: 1rem;
}
.cart-item-container {
  display: flex;

  padding: 20px 20px;
}
.item-image-contaner {
  margin-right: auto;
  display: none;
}
.cart-item-container img {
  height: 70px;
  width: 70px;
}
.content {
  margin-left: auto;
}
.clear-btn {
  display: flex;
  justify-content: center;
}

.clear-btn button {
  padding: 10px 10px;
  border: none;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  border-radius: 3px;
}
@media screen and (max-width: 768px) {
  .main-image {
    display: none;
  }
  .products-section {
    grid-column: 1/-1;
  }
  .open-cart {
    width: 100%;
  }
  .btn-grp button {
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-weight: 400;
  }

  .btn-grp {
    flex-direction: column;
    width: 100%;
  }
  .btn-grp p {
    align-self: flex-end;
  }
}
@media screen and (max-width: 500px) {
  .products-section {
    display: flex;
    flex-direction: column;
  }
  .product-card {
    height: 420px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  }

  .product-card img {
    height: 73%;
  }

  .button {
    padding: 20px 0px;
    width: 100%;
    display: block;
  }
}

.btn-con {
  display: flex;
  justify-content: center;
  align-items: center;
}
.checkout-btn {
  background-color: #000;
  color: #fff;
  padding: 10px 15px;
  cursor: pointer;
}

.check {
  color: #000;
}

